 .v-container .image-container{
    border : 8px solid #eeee;
    
}
.v-container>div{
    flex:1;
    
}
.v-right{
    gap:0.5rem
}
.accordion{
    margin-top:2rem;
    border:none

}
.accordionItem{
    border:0.8px solid #eee;
    background:white;
    border-radius:8px;
    overflow:hidden;
    margin-bottom:30px
}
.accordionButton{
    background:white;
    padding:1rem;
    width:100%;
    justify-content:space-between;
    cursor:pointer
}
.icon{
    padding:10px;
    background:#eeeeff;
    border-radius:5px
}
.icon svg{
    fill: orange
}
.accordionButton .primaryText{
    font-size:1.1rem
}
.accordionItem.expanded{
    box-shadow:var(--shadow);
    border-radius:10%
}
@media(max-width:1024px){
.v-container{
    flex-direction:column
}
@media(max-width:768px){
.accordionButton .primaryText{
    font-size:0.8rem
}
}
}